<template>
	<el-main class="main">
		<el-row :gutter="20">
			<el-col :span="2">
				<div class="grid-content ">电影地区表格</div>
			</el-col>
			<el-col :span="16">
				<el-col :span="10" class="text-right">
					<el-input v-model="search.keyword" prefix-icon="el-icon-search" :autofocus="true" placeholder="请输入关键字" size="small"></el-input>
					<el-button type="primary" icon="el-icon-search" size="small" style="margin-left: 10px;" round @click="searchData">查
						询</el-button>
					<el-button icon="el-icon-refresh-left" size="small" style="margin-left: 10px;" round @click="resetData">重 置</el-button>
				</el-col>
			</el-col>
			<el-col :span="6">
				<div class="grid-content text-right">
					<el-button type="primary" icon="el-icon-plus" size="small" @click="createOne">新 建</el-button>
					<el-tooltip class="item" effect="dark" content="刷新" placement="top">
						<el-button type="text" style="font-size: 20px;height: 33px;box-sizing: border-box;padding: 0; width: 40px;"
						 @click="refreshData">
							<i class="el-icon-refresh"></i>
						</el-button>
					</el-tooltip>
				</div>
			</el-col>
		</el-row>
		<el-container style="margin: 10px auto 12px;">
			<el-table :data="tableData.date" :stripe="true" v-loading="loading">
				<el-table-column type="index" width="50"></el-table-column>
				<el-table-column label="创建日期" width="200">
					<template slot-scope="scope">{{ scope.row.createdAt | dateFormat }}</template>
				</el-table-column>
				<el-table-column label="修改日期" width="200">
					<template slot-scope="scope">{{ scope.row.updatedAt | dateFormat }}</template>
				</el-table-column>
				<el-table-column prop="name" label="电影类型"></el-table-column>
				<el-table-column label="操作" width="142">
					<template slot-scope="scope">
						<el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
						<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>

		</el-container>

		<el-pagination background :page-size="limit" :current-page="search.page" layout="prev, pager, next" :total="tableData.total"
		 @current-change="currentChange"></el-pagination>

		<el-dialog :title="'当前类型：' + this.row.name" :visible.sync="dialogFormVisible" width="30%" @close="dialogClose">
			<el-form :model="form" @submit.native.prevent>
				<el-form-item label="类型名称:" :label-width="formLabelWidth">
					<el-input v-model="form.name" :autofocus="true" placeholder="请输入类型名称"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="confirmUpdate">确 定</el-button>
			</div>
		</el-dialog>

		<el-dialog title="新建类型" :visible.sync="createDialogForm" width="30%" @close="dialogClose">
			<el-form :model="form" @submit.native.prevent>
				<el-form-item label="类型名称:" :label-width="formLabelWidth">
					<el-input v-model="form.name" :autofocus="true" placeholder="请输入类型名称"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="createDialogForm = false">取 消</el-button>
				<el-button type="primary" @click="confirmCreate">确 定</el-button>
			</div>
		</el-dialog>
	</el-main>
</template>

<script>
	import country from '../services/country.js';
	import dayJS from 'dayjs';

	export default {
		name: 'CountryPage',
		filters: {
			dateFormat(e) {
				return dayJS(e).format('YYYY-MM-DD HH:mm:ss')
			}
		},
		watch: {
			'search.keyword'(value, old) {
				if (value.length <= 0) {
					this.refreshData();
				}
			},
			deep: true
		},
		data() {
			return {
				tableData: Array(),
				form: { //修改 创建
					name: '',
				},
				search: { //获取 查询
					keyword: '',
					page: 1
				},
				row: Object(),
				loading: false,
				dialogFormVisible: false,
				createDialogForm: false,
				formLabelWidth: '80px',
				limit: 13
			}
		},
		mounted() {
			this.getList();
		},
		methods: {
			currentChange(e) {
				this.search.page = e;
				this.refreshData();
			},
			handleEdit(index, row) {
				this.dialogFormVisible = true;
				this.row = row;
				this.form.id = row._id
			},
			async resetData() {
				if (this.search.keyword !== '') {
					this.search.keyword = '';
					await this.refreshData();
				}
			},
			searchData() {
				if (this.search.keyword !== '') {
					this.search.page = 1;
					this.refreshData();
				}
			},
			async refreshData() {
				this.loading = true;
				await this.getList();
				this.loading = false;
			},
			dialogClose(){
				delete this.form.id;
				this.form.name = '';
			},
			async confirmUpdate() {
				try {
					await this.$confirm(`确定求修改该项吗?`, '提示');
					this.loading = true;
					try {
						await country.update(this.form);
						this.dialogFormVisible = false
						this.$message.success('修改成功');
						this.getList();
					} catch (e) {
						this.$message.error(e.error || '修改失败');
					}
					this.loading = false;
				} catch (e) {}
			},
			async handleDelete(index, row) {
				try {
					await this.$confirm(`此操作将永久删除“${row.name}	”该项, 是否继续?`, '提示', {
						type: 'warning'
					});
					this.loading = true;
					try {
						await country.remove({
							id: row._id
						});
						this.$message.success('删除成功');
						this.refreshData();
					} catch (e) {
						this.$message.error(e.error || '删除失败');
					}
					this.loading = false;
				} catch (e) {}
			},
			async getList() {
				this.search.limit = this.limit;
				this.tableData = await country.get(this.search);
			},
			createOne() {
				this.createDialogForm = true;
			},
			async confirmCreate() {
				try {
					await country.create(this.form);
					this.$message.success('创建成功');
					this.refreshData();
					this.createDialogForm = false;
					this.form.name = '';
				} catch (e) {
					this.$message.error(e.error || '创建失败');
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.grid-content {
		line-height: 33px;
		font-size: 16px;
		color: rgba(0, 0, 0, .73);
		text-align: left;
	}

	.text-right {
		text-align: right;
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
	}
</style>
